import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import {
  getStudyClassificationsIn10,
  deleteStudyClassificationsIn10,
} from 'services/studyClassificationIn10';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DIEM_XEP_LOAI__XL_HOC_TAP_THANG_10__NHAP_DU_LIEU;

class PointLadderTenPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteStudyClassificationsIn10({ id });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Mã xếp loại',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Xếp loại',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Từ điểm',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'Đến điểm',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'Tên hệ',
        dataIndex: 'trainType',
        key: 'trainType',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link to={ROUTER_FORM_DATA}>
                <BtnAddNew />
              </Link>
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getStudyClassificationsIn10}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PointLadderTenPage />
  </Page>
);



